.article-navigation {
    padding: 30px 15px;
    .row {
        .navigation {
            width: 100%;
            display: flex;
            justify-content: center;
            flex-direction: column;
            h2 {
                align-self: center;
                width: 100%;
                text-align: center;
            }
            .nav-links {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                .nav-previous {
                    transition: all .2s ease-in-out;
                    background: $topmenu-background;
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    a {
                        transition: all .2s ease-in-out;
                        text-decoration: none;
                        text-align: center;
                        width: 100%;
                        padding: 10px 0;
                        color: $white;
                        display: flex;
                        justify-content: center;
                        &::before {
                            content: '\E001';
                            font-family: "bcFont";
                            margin-right: 15px;
                        }
                    }
                    &:hover {
                        background: $hover-color;
                        a {
                            color: $white;
                        }
                    }
                }
                .nav-next {
                    background: $topmenu-background;
                    display: flex;
                    justify-content: center;
                    width: 100%;
                    a {
                        display: flex;
                        justify-content: center;
                        transition: all .2s ease-in-out;
                        text-decoration: none;
                        text-align: center;
                        width: 100%;
                        padding: 10px 0;
                        color: $white;
                        &::after {
                            content: '\E002';
                            font-family: "bcFont";
                            margin-left: 15px;
                        }
                    }
                    &:hover {
                        background: $hover-color;
                        a {
                            color: $white;
                        }
                    }
                }
            }
        }
    }
}