/* Main style settings, that are used overall */
body {
    overflow-x: hidden;
    font-family: $baloo2-regular;
    .bc--main {
        padding: 0;
        margin: 0;
    }
}

.section {
    width: 100%;
}

.elementor-column-gap-default>.elementor-row>.elementor-column>.elementor-element-populated {
    padding: 0 !important;
}

::selection {
    background: $hover-color;
    color: $white;
}

/* Remove basic outline */
.btn:focus, .btn, input, input:focus, select, select:focus, img:focus, img {
    outline-style:none;
    box-shadow:none;
    border-color:transparent;
}

/* widget */
.widget {
    padding: 25px 0 !important;
    @include media('<=991px') {
        padding: 25px 15px !important;
    }
}

/* Remove outfill input color */
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
}


/* The to top arrow */
.top-arrow-container {
    transition: all .3s ease-in-out;
    box-shadow: 0 0 3px 0 $white;
    cursor: pointer;
    position: fixed;
    right: 2%;
    bottom: 2%;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background: $topmenu-background;
    display: flex;
    justify-content: center;
    span {
        transition: all .2s ease-in-out;
        width: 100%;
        height: 35px;
        font-size: 18px;
        align-self: center;
        transform: rotate(180deg);
        color: white;
        text-align: center;
        margin-bottom: 7px;
    }
    &:hover {
        background: $hover-color;
        span {
            margin-bottom: 12px;
        }
    }
}


/* The corner container */
.corner-container {
    transition: all .3s ease-in-out;
    max-height: 45px;
    max-width: 45px;
    width: 45px;
    height: auto;
    position: fixed;
    right: 0;
    top: 40%;
    background: $hover-color;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    overflow: hidden;
    .corner-container-toggle {
        width: 100%;
        text-align: center;
        align-self: center;
        color: $white;
        font-size: 28px;
    }
    .sidecontainer-list {
        transition: all .4s ease-in-out;
        list-style: none;
        margin: 0;
        padding: 0;
        opacity: 0;
        display: flex;
        justify-content: flex-start;
        flex-direction: column;
        a {
            cursor: pointer;
            text-decoration: none;
            align-self: center;
            color: $white;
            width: 100%;
            li {
                padding: 5px 0;
                width: 100%;
                display: flex;
                justify-content: center;
                span {
                    text-align: center;
                    font-size: 20px;
                }
            }
            &:hover {
                background: $topmenu-background;
            }
        }
    }
    &:hover {
        max-height: 500px;
        height: 181px;
        .sidecontainer-list {
            opacity: 1;
            width: auto;
        }
    }
}